import { html, LitElement } from "lit";
import { customElement, property } from "lit/decorators.js";
import { CONSTANTS } from "../../../constants";

/** Card To show payment platform link. */
@customElement('contitnue-to-pay')
export class ContinueToPay extends LitElement {
  constructor() {
    super();
    console.log({"this.visible": this.visible});
  }
  @property({ type: String })
  message?: string = "";

  @property({ type: String })
  token?: string = "";

  @property({ type: Boolean })
  visible?: boolean = null;

  @property({ type: Boolean })
  error?: boolean = null;

  render() {
    console.log({"this.visible": this.visible, "CONSTANTS.HTTPS.HEADERS": CONSTANTS.HTTPS.HEADERS});
    
    if (this.visible) {
      if (this.error) {
        return this._error("red");
      } else {
        return this._success("green");
      }
    } else {
      return html``;
    }
  }

  _success(color: string) {
    return html`
      <div class="w-full">
        <div class="my-4 block">
          <div class="block bg-${color}-50 pl-2 pr-4 py-2 rounded-md shadow-md">
            ${this._header(color)}
            <div class="text-sm uppercase flex justify-center my-2">
              <a type="button" class="inline-block px-5 py-3 rounded-lg shadow-md
              bg-green-900 text-sm text-white uppercase tracking-wider font-semibold"
                href="javascript:window.open('${CONSTANTS.URLS.PUBLIC_BASE_URL}pay?pay_token=${this.token}',
                  '_blank', 'width=640, height=480, status, toolbar=yes,scrollbars=yes,resizable=yes,top=500,
                  left=500, location')"> Click Here To Pay
              </a>
            </div>
          </div>
        </div>
      </div>
    `;      
  }

  _error(color: string) {
    return html`
      <div class="w-full">
        <div class="my-4 block">
          <div class="block bg-${color}-50 pl-2 pr-4 py-2 rounded-md shadow-md">
            ${this._header(color)}
          </div>
        </div>
      </div>
    `;      
  }

  _header(color: string) {
    const LeftSide = html`<div class="w-full text-${color}-700 uppercase text-sm">
      ${this.message}
    </div>`;
    const RightSide = html`<div class="w-2">${this.__closeBtn()}</div>`;
    return html`
      <div class="flex font-semibold">${LeftSide}${RightSide}</div>
    `;
  }

  __closeBtn() {
    return html`
      <div class="flex">
        <button type="button" class="text-red-600 font-semibold -mt-2
          inline-block py-1 px-2 rounded-md shadow-lg text-sm tracking-wider"
          @click="${this.__closeBtnAction}">x</button>
      </div>
    `;
  }

  __closeBtnAction(e: PointerEvent) {
    e.preventDefault();
    // console.log({e});

    this.visible = false;
  }

  createRenderRoot() {// this turns off shadow root
    return this;
  }
}