import { CONSTANTS } from "../../constants";
import { http } from "../../functions/http";
import { PayResponse_I } from "../../interfaces/client/pay";

export async function POST_PayActivationFee({
  clientId,
  accountTypeId,
  clientMembershipSizeId,
  clientSubscriptionDurationId,
}: POST_ActivationFee_I): Promise<PayResponse_I> {
  let payResponse: PayResponse_I = null;

  // console.log({
  //   accountTypeId,
  //   clientMembershipSizeId,
  //   clientSubscriptionDurationId,
  // });

  try {
    const postBody = JSON.stringify({
      "client_id": clientId,
      "account_type": accountTypeId,
      "client_membership_size": clientMembershipSizeId,
      "client_subscription_duration": clientSubscriptionDurationId
    });
    const response = await http(CONSTANTS.URLS.PAY_SUBSCRIPTION_FEE, {
      method: "POST",
      body: postBody,
    });
    console.log({ response });
    if (response.error === true) {
      let errorData: PayResponse_I;
      if (Array.isArray(response.data)) {

        const DATAS: Array<any> = response.data
        console.log({ DATAS });

        let errorMsg = "";
        DATAS.forEach((data) => {
          // console.log({ data });

          errorMsg += data.msg + "\n";

        });
        errorData = {
          error: {
            error: errorMsg,
          },
        }
      } else {
        errorData = {
          error: {
            error: response.msg,
          },
        }
      }
      payResponse = errorData;
    } else {
      let getPayResponse: PayResponse_I = {
        msg: response.msg,
        url: response.url,
      }
      console.log({ getPayResponse })
      payResponse = getPayResponse;
    }
    return payResponse;
  } catch (error) {
    console.error({ error });
    let errorData: PayResponse_I = {
      error: {
        error: CONSTANTS.ERROR_MSG,
      },
    };
    return errorData;
  }

}