import { html, LitElement } from "lit";
import { customElement, property } from "lit/decorators.js";
import { CONSTANTS } from "../../constants";

@customElement('is-loading')
export class IsLoading extends LitElement {
  
  @property({type: Number})
  size: number = 32;

  render() {
    return html`
      <div class="w-full block mb-2">
        <div class="flex justify-center">
            <div class="rounded-full overflow-hidden h-${this.size} w-${this.size} shadow-xl">
              <img src="${CONSTANTS.IMAGES.loadingGif3}" class="h-${this.size} w-${this.size} object-cover object-center">
            </div>
        </div>
      </div>
    `;
  }

  createRenderRoot() {
    return this;
  }
}
