import { html, LitElement } from "lit";
import { customElement, property } from "lit/decorators.js";
import { FormInput_I } from "../../interfaces/form";
import { FormSelectInput } from "./select";

@customElement("form-input")
export class FormInput extends LitElement {
  
  @property({ type: Object })
  input: FormInput_I = null;

  @property({ type: Boolean })
  error: boolean = false;

  @property({ type: Boolean })
  select: boolean = false;

  render() {
    const id = this.input.id;
    const name = this.input.name;
    const errorColor = this.error ? "text-red-400 border-red-600": "";
  
    this.select = ((this.select === null) || (this.select === undefined) || (this.select === false)) ? false : true;
    const inputClass1 = (this.input.inputClass === undefined)
      ? `appearance-none border rounded w-full py-2 px-3
      text-gray-700 leading-tight focus:outline-none bg- focus:shadow-inner
      bg-gray-50 ${errorColor}` : this.input.inputClass;
    const inputClass2 = (this.input.inputClass === undefined)
      ? `appearance-none block w-full bg-gray-100 text-gray-700
        border border-gray-200 rounded-md py-3 px-4 leading-tight
        focus:outline-none bg- focus:bg-white focus:border-gray-500 ${errorColor}`
      : this.input.inputClass;
  
    const inputType = (this.input.type === undefined)
      ? "text" : this.input.type;
  
    const label = (this.input.label === undefined)
      ? "Label" : this.input.label;
  
    const labelClass1 = (this.input.labelClass === undefined)
      ? "block text-gray-700 text-sm font-bold mb-2" : this.input.labelClass;
    const labelClass2 = (this.input.labelClass === undefined)
      ? "block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2"
      : this.input.labelClass;
  
    const disabled = (
      (this.input.disabled === null) ||
      (this.input.disabled === undefined) ||
      (this.input.disabled === false)) ? false : true;

      // console.log({ select });
  if (this.select === true) {
    return FormSelectInput({
      id: this.input.id,
      name: this.input.name,
      class: labelClass2,
      value: this.input.value,
      inputClass: inputClass2,
      label: label,
      labelClass: labelClass2,
      placeholder: this.input.placeholder,
      style: this.input.style,
      disabled: disabled,
      selectOptions: this.input.selectOptions
    });
  }

  if (disabled === false) {
    return html`
      <div class="w-full mb-4 md:mb-6">
        <label class="${labelClass2}"> ${label}
          <input type="${inputType}" style="${this.input.style}" class="${inputClass2}" placeholder="${this.input.placeholder}"
            id="${id}" name="${name}" value!="${this.input.value}"/>
        </label>
      </div>
    `;
  } else {
    return html`
      <div class="w-full mb-4 md:mb-6">
        <label class="${labelClass2}"> ${label}
          <input type="${inputType}" style="${this.input.style}" ?disabled="${this.input.disabled}" class="${inputClass2}"
            placeholder="${this.input.placeholder}" id="${id}" name="${name}" value="${this.input.value}"/>
        </label>
      </div>
    `;
  }
  }

  createRenderRoot() {// this turns off shadow root
    return this;
  }
}