import { CONSTANTS } from "../../constants";
import { http } from "../../functions/http";
import { ClientActivationFee_I } from "../../interfaces/client/fee";


export async function GET_ClientActivationFee({
  clientId,
  noDuration,
  durationId,
  accountTypeId,
  clientMembershipSizeId
}: GET_ClientActivationFee_I) {
  let clientActivationFee: ClientActivationFee_I = null;

  try {
    // const response = await http(`/images/fee.json?account_type_id=${accountTypeId}&no_duration=${noDuration}&client_id=${clientId}&size_id=${clientMembershipSizeId}`);
    const response = await http(CONSTANTS.URLS.GET_SUBSCRIPTION_FEE +
      "?account_type_id=" + accountTypeId + "&no_duration=" + noDuration +
      "&client_id=" + clientId + "&size_id=" + clientMembershipSizeId +
      "&duration_id=" + durationId
    );
    // console.log({ response });
    if (response.error === true) {
      let errorData: ClientActivationFee_I = {
        error: {
          error: response.msg,
        },
      }
      clientActivationFee = errorData;
    } else {
      const DATAS: any = response.data
      let getClientActivationFee: ClientActivationFee_I = {
        fee: DATAS.fee,
      }
      clientActivationFee = getClientActivationFee
    }
  } catch (error) {
    console.error({ error });
    let errorData:  ClientActivationFee_I = {
      error: {
        error: CONSTANTS.ERROR_MSG,
      },
    };
    return errorData;
  }
  // console.log({ clientActivationFee })
  return clientActivationFee;
}