import { html, LitElement } from 'lit';
import { customElement, property } from 'lit/decorators.js';
import { CONSTANTS } from '../../constants';

@customElement('app-header')
export class AppHeader extends LitElement {

  constructor(header?: headerParams) {
    super();
    this.title = header?.title;
  }

  @property({ type: String })
  title: string = '';

  render() {    
    return html`
      <header class="p-4 w-full shadow-xl bg-white fixed top-0 left-0 right-0 z-10">
        <div class="flex">
          <div class="w-1/3 relative">
            <img src="${CONSTANTS.IMAGES.akwaabaLogo}" class="absolute inset-0 h-8 w-auto object-contain object-center">
          </div>
          <div class="w-2/3 pl-4">
            <h1 class="font-bold text-gray-700 uppercase">
              ${this.title}
            </h1>
          </div>
        </div>
      </header>
    `;
  }


  createRenderRoot() {// this turns off shadow root
    return this;
  }
}

interface headerParams {
  title?: string;
}
