import { html, LitElement } from 'lit';
import { customElement, property } from 'lit/decorators.js';
import { http } from '../../functions/http';
import { ClientInfo_I } from '../../interfaces/client';
import { Type_I } from '../../interfaces/client/type';
import { GET_ClientInfo } from '../../network/client/info';
import './client_not_found/index';
import './client_banner/banner';
import './activation/form';
import '../../widgets/header/index';
import '../../widgets/footer/index2';
import { CONSTANTS } from '../../constants';

@customElement('app-view')
export class AppView extends LitElement {

  constructor() {
    super();
    this.clientID = Number(
      CONSTANTS.HTTPS.GET.get("client")?.value
    );
    console.log({ "this.clientID": isNaN(this.clientID) });

    if (!isNaN(this.clientID)) {
      GET_ClientInfo(this.clientID).then((value) => {
        this.clientInfo = value;
      });
    }
  }

  @property({ type: Number })
  clientID: number = 0;

  @property({ type: Object })
  clientInfo: ClientInfo_I = null;

  render() {
    if (isNaN(this.clientID) || this.clientID === 0) {
      console.log({ "this.clientID": this.clientID });
      return html`
        <app-header title="Account Renewal"></app-header>
        <main class="mt-14 mb-28 p-4 pb-48 h-screen">
          <div class="mt-4">
            <client-not-found></client-not-found>
          </div>
        </main>
        <app-footer2></app-footer2>
      `;
    } else {
      return html`
        <app-header title="Account Renewal"></app-header>
        <main class="mt-14 mb-28 p-4 pb-48 h-screen">
          <div class="mt-4">
            <client-banner .clientInfo="${this.clientInfo}"></client-banner>
          </div>
          <div class="mt-4">
            <activation-form .clientInfo="${this.clientInfo}"></activation-form>
          </div>
        </main>
        <app-footer2></app-footer2>
      `;
    }
  }

  // getClientInfo(): ClientInfo_I {
  async getClientInfo() {
    let clientInfo: ClientInfo_I = null;

    try {
      const response = await http('/images/sample.json');
      // console.log({ response });
      if (response.error === true) {
        let errorData: ClientInfo_I = {
          error: {
            error: response.msg,
          },
        }
        clientInfo = errorData;
      } else {
        const DATAS: Array<any> = response.data
        // console.log({ DATAS });

        DATAS.forEach((data) => {
          // console.log({ data });
          let subscriptionDurations: Array<Type_I> = [];

          const durations: Array<any> = data.subscriptionDurations;
          durations.forEach((duration) => {
            subscriptionDurations.push({ id: duration.id, name: duration.name, });
          });

          let getClientInfo: ClientInfo_I = {
            name: data.name,
            image: data.image,
            isSpecial: data.isSpecial,
            accountType: data.accountType,
            membershipSize: data.membershipSize,
            subscriptionDurations: subscriptionDurations,
          }
          // console.log({ getClientInfo })
          clientInfo = getClientInfo;
        });
      }
    } catch (error) {
      console.error({ error })
    }
    // console.log({ clientInfo })
    this.clientInfo = clientInfo;
    return clientInfo;
  }

  createRenderRoot() {// this turns off shadow root
    return this;
  }
}
