import { html } from "lit";
import { FormInput_I } from "../../interfaces/form";

export const FormSelectInput = (input: FormInput_I) => {
  // console.log({ input });

  const id = input.id;
  const name = input.name;
  const selectOptions = (input.selectOptions === null || input.selectOptions === undefined) ? false : true;

  const selectInput = html`
    <div class="w-full mb-4 md:mb-6">
      <label class="${input.labelClass}"> ${input.label}
        <select style="${input.style}" ?disabled="${input.disabled}" class="${input.inputClass}" placeholder="${input.placeholder}" id="${id}"
          name="${name}">
          ${(selectOptions === false) ? "" : input.selectOptions.map((option) => {
            const selected = (
              (option.selected === null) ||
              (option.selected === undefined) ||
              (option.selected === false)
              ) ? false : true;
            // console.log({ selected });
            
            if (input.disabled === false) {
              if (selected) {
                return html`<option value="${option.value}" ?selected="${option.selected}">
                  ${option.title}
                </option>`;
              } else {
                return html`<option value="${option.value}">
                  ${option.title}
                </option>`;
              }
            } else {
              if (selected) {
                return html`<option value="${option.value}" ?disabled="${input.disabled}" ?selected="${option.selected}">
                  ${option.title}
                </option>`;
              } else {
                return html`<option value="${option.value}" ?disabled="${input.disabled}">
                  ${option.title}
                </option>`;
              }
            }
            })}
        </select>
      </label>
    </div>
  `;
  return selectInput;
}