import { html, LitElement } from 'lit';
import { customElement } from 'lit/decorators.js';

import { AppView } from './app';

@customElement('akwaaba-app-general-account-activation')
export class AkwaabaAppGeneralAccountActivation extends LitElement {

  render() {
    return html`
      <div class="antialiased text-gray-900 bg-gray-50">
        ${new AppView()}
      </div>
    `;
  }


  createRenderRoot() {// this turns off shadow root
    return this;
  }
}