import { CONSTANTS } from "../../constants";
import { http } from "../../functions/http";
import { ClientInfo_I } from "../../interfaces/client";
import { Type_I } from "../../interfaces/client/type";

export async function GET_ClientInfo(clientId: Number) {
  let clientInfo: ClientInfo_I = null;

  try {
    // const response = await http('/images/sample.json');
    const response = await http(CONSTANTS.URLS.GET_CLIENT_INFO + "?client_id=" + clientId);
    // console.log({ response });
    if (response.error === true) {
      let errorData: ClientInfo_I = {
        error: {
          error: response.msg,
        },
      }
      clientInfo = errorData;
    } else {
      const DATAS: Array<any> = response.data
      console.log({ DATAS });

      DATAS.forEach((data) => {
        // console.log({ data });
        let subscriptionDurations: Array<Type_I> = [];

        const durations: Array<any> = data.subscriptionDurations;
        durations.forEach((duration) => {
          subscriptionDurations.push({ id: duration.id, name: duration.info, });
        });

        const size: any = data.membershipSize;
        const membershipSize: Type_I = {
          id: size.id, name: size.info,
        };

        const type: any = data.accountType;
        const accountType: Type_I = {
          id: type.id, name: type.info,
        };

        let getClientInfo: ClientInfo_I = {
          name: data.name,
          image: data.image,
          clientId: data.clientId,
          isSpecial: data.isSpecial,
          accountType: accountType,
          membershipSize: membershipSize,
          subscriptionDurations: subscriptionDurations,
        }
        // console.log({ getClientInfo })
        clientInfo = getClientInfo;
      });
    }
  } catch (error) {
    console.error({ error });
    let errorData:  ClientInfo_I = {
      error: {
        error: CONSTANTS.ERROR_MSG,
      },
    };
    return errorData;
  }
  // console.log({ clientInfo })
  return clientInfo;
}