import { GetParams_I } from "../interfaces/http";

const _request = new XMLHttpRequest();
const _url = new URL(document.location.href);

_request.open('HEAD', _url.href, false);
_request.send(null);

// console.log({_request});

const params = new URLSearchParams(_url.search);
let _GET: Array<GetParams_I> = [];

class _GET_ {
  constructor(params: Array<GetParams_I>) {
    this.params = params;
  }
  params: Array<GetParams_I> = [];

  public get(param: string): GetParams_I {
    let _param: GetParams_I;
    this.params.forEach((p) => {
      if (p.param === param) {
        _param = p;
      }
    });
    return _param;
  }
}

params.forEach((v, k) => {
  const param: GetParams_I = {
    param: k, value: v
  };
  if (!_GET.includes(param)) {
    _GET.push(param);
  }
});

export const HTTPS = {
  GET: new _GET_(_GET),
  HEADERS: _request.getAllResponseHeaders()
};