import { html, LitElement } from 'lit';
import { customElement } from 'lit/decorators.js';
import { CONSTANTS } from '../../../constants';
import '../../../widgets/loading';

@customElement('client-not-found')
export class ClientNotFound extends LitElement {

  constructor() {
    super();
  }

  render() {
    return html`
    <diV class="flex justify-around">
      <div class="relative px-4 mt-10">
        <div class="rounded-md overflow-hidden h-80 w-80 shadow-xl">
          <img src="${CONSTANTS.IMAGES.errorNotFound}" class="h-80 w-80 object-cover object-center">
        </div>
        <div class="relative px-4 -mt-2">
          <div class="bg-white px-4 py-2 rounded-lg border shadow-md">
            <h4 class="font-semibold text-lg text-center uppercase tracking-wider">client not found</h4>
          </div>
        </div>
      </div>
    </diV>
  `;
  }


  createRenderRoot() {// this turns off shadow root
    return this;
  }
}