import { html, css, LitElement } from 'lit';
import '../../../widgets/loading';
import '../../../widgets/form/input';
import '../continue/pay';
import '../payment_status/status';
import { ClientInfo_I } from '../../../interfaces/client';
import { customElement, property, query } from 'lit/decorators.js';
import { POST_PayActivationFee } from '../../../network/activation/pay';
import { PayResponse_I } from '../../../interfaces/client/pay';
import { FormInput_I } from '../../../interfaces/form';
import { GET_ClientActivationFee } from '../../../network/activation/fee';
import { ClientActivationFee_I } from '../../../interfaces/client/fee';
import { Type_I } from '../../../interfaces/client/type';

@customElement('activation-form')
export class ActivationForm extends LitElement {

  constructor(clientInfo?: ClientInfo_I) {
    super();

    this.clientInfo = clientInfo;
  }

  @query('#account_type')
  _accountType: any;

  @query('#client_membership_size')
  _clientMembershipSize: any;

  @query('#client_subscription_duration')
  _clientSubscriptionDuration: any;

  @query('#client_subscription_fee')
  _clientSubscriptionFee: any;

  @query('#pay-is-loading')
  _payIsLoading: any;

  @query('#get-fee-is-loading')
  _getFeeIsLoading: any;

  @query('#form-submit-button')
  _formSubmitButton: any;

  @property({ type: Object })
  clientInfo?: ClientInfo_I = null;

  @property({ type: Boolean })
  subscriptionFeeError?: boolean = false;

  @property({ type: String })
  subscriptionFee?: string = "GHC 00.00";

  @property({ type: Array })
  subscriptionDurations?: Type_I[] = [];

  @property({ type: Object })
  _selectOption?: Type_I = {
    id: 0, name: "Select Subscription Duration"
  };

  @property({ type: Boolean })
  _paySuccess?: boolean = false;

  @property({ type: String })
  _paySuccessToken?: string = "";

  @property({ type: String })
  _paySuccessMessage?: string = "";

  @property({ type: Boolean })
  _paySuccessError?: boolean = false;

  @property({ type: Boolean })
  _paySuccessVisible?: boolean = false;

  @property({ type: Boolean })
  _payIsLoadingVal?: boolean = false;

  @property({ type: Boolean })
  _getFeeIsLoadingVal?: boolean = false;
  
  render() {    
    if (this.clientInfo === null) {
      return html`<is-loading></is-loading>`;
    } else {
      let subscriptionDurations = this.clientInfo.subscriptionDurations;
      if (Array.isArray(subscriptionDurations)) {
        if (!subscriptionDurations.includes(this._selectOption)) {
          subscriptionDurations.unshift(this._selectOption)
        }
        return html`
          <diV class="flex justify-around -mt-6">
            <div class="w-full md:w-4/6 lg:w-7/12 xl:w-4/12">
              <form class="bg-white border rounded px-5 pt-6 pb-8 mb-4"
                action="" method="GET" @submit="${this.payActivationFee}">
                
                <form-input .input="${this.formInput_INPUT({
                  id: "account_type", name: "account_type",  label: "Account Type",
                  placeholder: "Account Type", selectOptions: [ {
                      title: this.clientInfo.accountType.name,
                      value: this.clientInfo.accountType.id, selected: true
                    },
                  ]
                })}" .select="${true}"></form-input>
                <form-input .input="${this.formInput_INPUT({
                  id: "client_membership_size", name: "client_membership_size",
                  label: "Membership Size", placeholder: "Membership Size",
                  selectOptions: [ {
                      title: this.clientInfo.membershipSize.name,
                      value: this.clientInfo.membershipSize.id, selected: true
                    },
                  ]
                })}" .select="${true}"></form-input>
                <form-input .input="${this.formInput_INPUT({
                  id: "client_subscription_duration", name: "client_subscription_duration",
                  label: "Select Subscription Duration", placeholder: "Select Subscription Duration",
                  selectOptions: subscriptionDurations.map((duration) => {
                    return { title: duration.name, value: duration.id, };
                  }),
                })}" .select="${true}" @change="${this.getSubscriptionFee}"></form-input>
                <is-loading size="16" id="get-fee-is-loading" class="${this._getFeeIsLoadingVal? 'block': 'hidden'}"></is-loading>
                <form-input .input="${this.formInput_INPUT({
                  id: "client_subscription_fee", name: "client_subscription_fee",
                  label: "Subscription Fee", placeholder: "Subscription Fee",
                  disabled: true, value: this.subscriptionFee
                })}" .select="${false}" .error="${this.subscriptionFeeError}"></form-input>
                ${this._paySuccess? html`<div class="items-center">
                    <contitnue-to-pay class="w-full" message="${this._paySuccessMessage}" 
                    token="${this._paySuccessToken}" ?visible="${this._paySuccessVisible}"
                    ?error="${this._paySuccessError}"></contitnue-to-pay>

                    ${this._paySuccessError ? html`` : html`<payment-status class="w-full" 
                    token="${this._paySuccessToken}" clientId="${this.clientInfo.clientId}" 
                    ?visible="${this._paySuccessVisible}">
                  </payment-status>`} </div>`: html``
                }
                <is-loading size="16" id="pay-is-loading" class="${this._payIsLoadingVal? 'block': 'hidden'}"></is-loading>
                <div class="flex justify-center">
                  <button type="submit"
                    class="inline-block px-5 py-3 rounded-lg shadow-md bg-green-600 text-sm text-white uppercase tracking-wider font-semibold">
                    Pay Renewal Fee
                  </button>
                </div>
              </form>
            </div>
          </diV>
        `;
      }
      return html``;
    }
  }

  formInput_INPUT(input: FormInput_I) {
    // console.log({"input: FormInput_I":input});
    
    return input;
  }

  async getSubscriptionFee(e: Event) {
    e.preventDefault();
    
    this._getFeeIsLoadingVal = true;

    // console.log({
    //   e: e.currentTarget,
    //   "_clientSubscriptionDuration": this._clientSubscriptionDuration.value
    // });
    const clientActivationFee:ClientActivationFee_I = await GET_ClientActivationFee({
      noDuration: false,
      clientId: this.clientInfo.clientId,
      accountTypeId: this._accountType.value,
      durationId: this._clientSubscriptionDuration.value,
      clientMembershipSizeId: this._clientMembershipSize.value,

    });
    // console.log({
    //   clientActivationFee,
    //   "clientActivationFee.fee": clientActivationFee.error
    // });
    if (clientActivationFee.error !== undefined) {
      this.subscriptionFeeError = true;
      this.subscriptionFee = "Error! " + clientActivationFee.error.error;
    } else {
      this.subscriptionFeeError = false;
      this.subscriptionFee = "GHC " + clientActivationFee.fee;
    }
    this._getFeeIsLoadingVal = false;
  }

  async payActivationFee(e: PointerEvent) {
    e.preventDefault();
    console.log({ e });
    this._payIsLoadingVal = true;
    this._paySuccessVisible = false;
    this._paySuccessError = false;
    
    const payActivationFee:PayResponse_I = await POST_PayActivationFee({
      clientId: this.clientInfo.clientId,
      accountTypeId: this._accountType.value,
      clientMembershipSizeId: this._clientMembershipSize.value,
      clientSubscriptionDurationId: this._clientSubscriptionDuration.value,
    });
    this._paySuccess = true;
    this._paySuccessVisible = true;
    console.log({ payActivationFee: payActivationFee });

    if ((payActivationFee.error === undefined) ||
      (payActivationFee.error === null) ) {
        this._paySuccessToken = payActivationFee.url;
        this._paySuccessMessage = payActivationFee.msg;
    } else {
      this._paySuccessError = true;
      this._paySuccessMessage = payActivationFee.error.error;
    }
    this._payIsLoadingVal = false;
  }

  createRenderRoot() {// this turns off shadow root
    return this;
  }
}