export async function http(input: RequestInfo, init?: RequestInit) {
  const status = (response: any) => {
    if (response.status >= 200 && response.status < 300) {
      return Promise.resolve(response)
    } else {
      return Promise.reject(new Error(response.statusText))
    }
  }

  const json = (response: any) => {
    return response.json()
  }
  // console.log({init});

  var _init: RequestInit = { method: "GET" };

  init = (init === null) || (init === undefined) ? _init : init;

  var myHeaders = new Headers();
  myHeaders.append("Access-Control-Allow-Origin", "*/*");
  myHeaders.append("Accept", "application/json, text/plain, */*");
  myHeaders.append("Authorization", "Bearer dskjsdr43930934j34934304934n34kkd3d89sdjsdjb349");
  myHeaders.append("Content-Type", "application/json");

  // var myHeaders = {
  //   "Access-Control-Allow-Origin":"*/*",
  //   "Accept":"application/json, text/plain, */*",
  //   "Authorization":"Bearer dskjsdr43930934j34934304934n34kkd3d89sdjsdjb349",
  // }
  init.headers = myHeaders;
  init.mode = "cors";
  
  // init.redirect = 'follow';
  console.log({init});

  return await fetch(input, init)
    .then(status)
    .then(json)
    .then(async (data) => {
      // console.log('Request succeeded with JSON response', data);
      return data;
    }).catch(function (error) {
      console.error('Request failed', error);
      throw error;
    });
}