import { html, LitElement } from 'lit';
import { customElement, property } from 'lit/decorators.js';
import { ClientInfo_I } from '../../../interfaces/client';
import '../../../widgets/loading';

@customElement('client-banner')
export class ClientBanner extends LitElement {

  constructor(clientInfo: ClientInfo_I) {
    super();

    this.clientInfo = clientInfo;
  }

  @property({ type: Object })
  clientInfo: ClientInfo_I = null;

  render() {
    if (this.clientInfo === null) {
      return html`<is-loading></is-loading>`;
    } else {
      // console.log({
      //   "this.clientInfo": this.clientInfo,
      //   "this.clientInfo.error": this.clientInfo.error
      // })
      if (this.clientInfo.error === undefined) {
        return html`
        <diV class="flex justify-around">
          <div class="w-2/3 md:w-7/12 lg:w-5/12 xl:w-3/12">
            <div class="relative pb-5/12 md:pb-5/12">
              <img src="${this.clientInfo.image}" alt="${this.clientInfo.name} Logo"
                class="h-full w-full object-cover object-center rounded-lg absolute inset-0 border">
            </div>
            <div class="relative px-4 -mt-2">
              <div class="bg-white px-4 py-2 rounded-lg border shadow-md">
                <h4 class="font-semibold text-lg text-center">${this.clientInfo.name}</h4>
              </div>
            </div>
          </div>
        </diV>
      `;
      } else {
        return html`
          <h1 class="text-red-500 font-bold tracking-wide text-center">
            ${this.clientInfo.error.error}...
          </h1>
        `;
      }
    }
  }


  createRenderRoot() {// this turns off shadow root
    return this;
  }
}