import { CONSTANTS } from "../../constants";
import { http } from "../../functions/http";
import { ClientPaymentStatus_I } from "../../interfaces/client/payment_status";


export async function GET_ClientPaymentStatus({
  clientId,
  token
}: GET_ClientPaymentStatus_I) {
  let clientPaymentStatus: ClientPaymentStatus_I = null;

  try {
    const response = await http(CONSTANTS.URLS.GET_PAYMENT_STATUS +
      "?client_id=" + clientId + "&pay_token=" + token
    );
    // console.log({ response });
    if (response.error === true) {
      let errorData: ClientPaymentStatus_I = {
        error: {
          error: response.msg,
        },
      }
      clientPaymentStatus = errorData;
    } else {
      let getClientPaymentStatus: ClientPaymentStatus_I = {
        msg: response.msg,
      }
      clientPaymentStatus = getClientPaymentStatus
    }
  } catch (error) {
    console.error({ error });
    let errorData:  ClientPaymentStatus_I = {
      error: {
        error: CONSTANTS.ERROR_MSG,
      },
    };
    return errorData;
  }
  // console.log({ clientPaymentStatus })
  return clientPaymentStatus;
}