import { html, LitElement } from "lit";
import { customElement, property } from "lit/decorators.js";
import { CONSTANTS } from "../../../constants";
import { ClientPaymentStatus_I } from "../../../interfaces/client/payment_status";
import { GET_ClientPaymentStatus } from "../../../network/activation/payment_status";
import '../../../widgets/loading';

/** Card To show payment payment. */
@customElement('payment-status')
export class PaymentStatus extends LitElement {
  constructor() {
    super();
    console.log({ "this.visible": this.visible });

    this._loading = true;
    setInterval(() => {
      if (this._fetch) {
        this.getPaymentStatus().then((value) => {
  
          this._loading = false;
        });
      }
    }, 2000);
  }

  @property({ type: Boolean })
  _fetch?: boolean = true;

  @property({ type: String })
  _message?: string = "";

  @property({ type: Number })
  clientId?: number = 0;

  @property({ type: String })
  token?: string = "";

  @property({ type: Boolean })
  visible?: boolean = null;

  @property({ type: Boolean })
  _error?: boolean = null;

  @property({ type: Boolean })
  _loading?: boolean = false;

  render() {
    console.log({ "this.visible": this.visible, "CONSTANTS.HTTPS.HEADERS": CONSTANTS.HTTPS.HEADERS });

    if (this.visible) {
      if (this._error) {
        return this._return("blue");
      } else {
        return this._return("green");
      }
    } else {
      return html``;
    }
  }

  _return(color: string) {
    return html`
      <div class="w-full">
        <div class="my-4 block">
          <div class="block bg-${color}-50 pl-2 pr-4 py-2 rounded-md shadow-md">
            ${this._header(color)}
          </div>
        </div>
      </div>
    `;
  }

  _header(color: string) {
    const LeftSide = html`<div class="w-full text-${color}-700 uppercase text-sm">
  ${(this._loading) ? html`<is-loading size="16"></is-loading>` : this._message}
</div>`;
    const RightSide = html`<div class="w-2">${this.__closeBtn()}</div>`;
    return html`
      <div class="flex font-semibold">${LeftSide}${RightSide}</div>
    `;
  }

  __closeBtn() {
    return html`
      <div class="flex">
        <button type="button" class="text-red-600 font-semibold -mt-2
                inline-block py-1 px-2 rounded-md shadow-lg text-sm tracking-wider"
          @click="${this.__closeBtnAction}">x</button>
      </div>
    `;
  }

  __closeBtnAction(e: PointerEvent) {
    e.preventDefault();
    // console.log({e});

    this.visible = false;
  }

  async getPaymentStatus() {

    // console.log({
    //   e: e.currentTarget,
    //   "_clientSubscriptionDuration": this._clientSubscriptionDuration.value
    // });
    const clientPaymentStatus: ClientPaymentStatus_I = await GET_ClientPaymentStatus({
      clientId: this.clientId,
      token: this.token,

    });
    console.log({
      clientPaymentStatus,
    });
    if (clientPaymentStatus.error !== undefined) {
      this._error = true;
      this._message = "! " + clientPaymentStatus.error.error;
    } else {
      this._error = false;
      this._fetch = false;
      this._message = clientPaymentStatus.msg;
    }
  }

  createRenderRoot() {// this turns off shadow root
    return this;
  }
}